.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* global css */


button{
  width: 100%;
  margin: 5px 0;
  padding: 5px;
  color: white;
  background: purple;
  border: none;
  border-radius: 5px;
}
button:disabled{
  opacity: 0.7;
}

input{
  margin-bottom: 10px;
  padding: 5px;
}